* {
  box-sizing: border-box;
}

h1 {
  font-weight: 200;
}

body {
  font-family: "Inter", sans-serif;
  margin: 0;
  transition: background-color 0.3s ease;
}

.main-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  transition: background-color 0.3s ease;
}

.calculator-button {
  border: none;
  border-radius: 50%;
  color: #fff;
  padding: 0;
  font-size: 24px;
  line-height: 1;
  font-family: "Open Sans", sans-serif;
  margin: 5px;
  height: 52px;
  width: 52px;
  flex: 1 1 0;
  outline: none !important;
  cursor: pointer;
  transition: all 0.2s ease;
}

.calculator-wrapper {
  transition: background-color 0.3s ease;
  border-radius: 20px;
  width: 300px;
  padding: 10px;
  outline: none;
}

.calculator-screen {
  transition: border-color 0.3s ease;
  border-radius: 20px;
  border: 3px solid #e3d26f;
  padding: 10px;
  margin-bottom: 15px;
  color: #fff;
  text-align: right;
  font-weight: 700;
  font-size: 32px;
  letter-spacing: 1px;
  overflow: hidden;
}

.calculator-buttons {
  transition: border-color 0.3s ease;
  border-radius: 20px;
  border: 3px solid #e3d26f;
  padding: 10px;
  display: flex;
}

.calculator-button--clear {
  flex-grow: 3;
  border-radius: 45px;
}

.calculator-button--clear:hover {
  background: hsl(14, 38%, 60%);
}

.calculator-button--zero {
  flex-grow: 2;
  border-radius: 45px;
}

.calculator-button--digit {
  transition: background-color 0.3s ease;
}

.calculator-buttons__digits {
  flex-grow: 3;
  display: flex;
  flex-direction: column;
}

.calculator-button--action {
  color: #3f0d12;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.calculator-button--action img {
  max-width: 20px;
  max-height: 20px;
}

.calculator-buttons__row {
  display: flex;
}

.calculator-buttons__actions {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.calculator-screen__history {
  font-weight: 300;
}

.color-btn {
  margin-top: 50px;
  background: #fff;
  border: none;
  border-radius: 30px;
  font-weight: 200;
  font-family: Inter;
  padding: 10px 20px;
  font-size: 27px;
  text-transform: uppercase;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
  outline: none;
  cursor: pointer;
}

.header {
  margin-bottom: 50px;
}

@media screen and (max-width: 767px) {
  .main-wrapper {
    padding: 15px;
    text-align: center;
  }

  h1 {
    margin: 0;
  }

  .header {
    margin-bottom: 30px;
  }

  .color-btn {
    margin-top: 30px;
    font-size: 21px;
  }
}
